@import './footer.scss';
@import './menu.scss';

html,
body,
div,
h1,
h2,
h3,
h4,
h5 {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0.5em;
  font-family: sans-serif;
}

h1 {
  font-size: 1em;
}

.container {
  width: 980px;
  max-width: 100%;
  margin: 0 auto;
  padding: 0.5em;
}

.main-img {
  background-image: url('../img/thumbnail.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  position: relative;
  width: 90%;
  height: 600px;
  border-radius: 1em;
  margin: 0.5em 0;
  margin: 0 auto;
}
$icon-size: 64px;

.icon {
  width: $icon-size;
  height: $icon-size;
  border-radius: $icon-size/2;
  position: absolute;
  top: 1em;
  left: 1em;
}
